<template>
  <div class="main-header">
    <a-button type="primary" @click="$store.dispatch('changeCollapsed')">
      <a-icon :type="$store.state.collapsed ? 'menu-unfold' : 'menu-fold'" />
    </a-button>
    <div class="breadcrumb">
      <template v-if="currentRoute.length > 1">
        <a-breadcrumb>
          <a-breadcrumb-item>{{currentRoute[0].meta.title}}</a-breadcrumb-item>
          <a-breadcrumb-item>
            <router-link :to="{name: currentRoute[1].name}">{{currentRoute[1].meta.title}}</router-link>
          </a-breadcrumb-item>
        </a-breadcrumb>
      </template>
    </div>
    <ul class="user-info">
      <li>
        欢迎您~{{$store.state.user.user ? $store.state.user.user.username : ''}}
        <a-icon type="down" />
      </li>
      <li @click="changeInfo">修改信息</li>
      <li @click="logout">退出</li>
    </ul>
    <a-modal
      title="修改个人信息"
      :visible="visible"
      :confirm-loading="confirmLoading"
      @ok="handleOk"
      okText="修改"
      cancelText="取消"
      @cancel="close"
    >
      <a-form-model
        :model="form"
        ref="form"
        :rules="rules"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-form-model-item label="邮箱" prop="email">
          <a-input v-model="form.email" disabled />
        </a-form-model-item>
        <a-form-model-item label="用户名" prop="username">
          <a-input v-model="form.username" />
        </a-form-model-item>
        <a-form-model-item label="密码" prop="password">
          <a-input v-model="form.password" />
        </a-form-model-item>
        <a-form-model-item label="新密码" prop="newPassword">
          <a-input v-model="form.newPassword" />
        </a-form-model-item>
        <a-form-model-item label="验证码" prop="code">
          <a-input v-model="form.code" :maxLength="6">
            <span
              slot="suffix"
              :style="{cursor: 'pointer',
                      'user-select': 'none',
                      'font-weight': 600,
                      color: '#30a679',}"
              @click="getCode"
            >获取验证码</span>
          </a-input>
        </a-form-model-item>
        <a-form-model-item label="角色">
          <a-select v-model="form.role" placeholder="please select your zone">
            <a-select-option value="customer">普通用户</a-select-option>
            <a-select-option value="admin">管理员</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
import userApi from "@/api/user";
import reg from "@/utils/public/regular";
export default {
  data() {
    return {
      currentRoute: "",
      visible: false, // 弹框显示
      confirmLoading: false, // 异步关闭
      form: {
        email: this.$store.state.user.user.email, // 邮箱
        password: "", // 密码
        newPassword: "", // 新密码
        code: "", // 验证码
        username: "", // 用户名
        role: "customer", // 角色
      },
      labelCol: { span: 6 },
      wrapperCol: { span: 14 },
      rules: {
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          {
            pattern: reg.email,
            message: "请输入正确的邮箱格式",
            trigger: "blur",
          },
        ],
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        code: [{ required: true, message: "请输入验证码", trigger: "blur" }],
      },
    };
  },
  methods: {
    async logout() {
      const res = await userApi.logout();
      if (res.status === "success") {
        this.$store.dispatch("user/logout");
        this.$router.push({
          name: "Login",
        });
      }
    },
    // 打开信息修改弹窗
    changeInfo() {
      this.visible = true;
    },
    // 修改
    handleOk() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.confirmLoading = true;
          userApi.changeInfo(this.form).then((res) => {
            if (res.status === "success") {
              // 修改成功
              this.$message.success(res.msg);
              this.visible = false;
              this.confirmLoading = false;
            } else {
              this.$message.warn(res.msg);
            }
          });
        }
      });
    },
    async getCode() {
      this.$refs.form.validateField("email", (valid) => {
        if (!valid) {
          userApi.getCode(this.form.email).then((res) => {
            if (res.status === "success") {
              this.$message.success("验证码发送成功");
            } else {
              this.$message.warn(res.msg);
            }
          });
        }
      });
    },
    // 关闭弹框
    close(){
      this.visible = false;
    }
  },
  watch: {
    $route() {
      this.currentRoute = this.$router.currentRoute.matched;
    },
  },
};
</script>

