<template>
  <div class="login-container">
    <div class="login">
      <h2 class="title">每日优鲜后台管理系统</h2>
      <a-form-model :model="loginForm" ref="loginForm" :rules="rules">
        <a-form-model-item prop="email">
          <a-input v-model="loginForm.email" class="input" placeholder="请输入邮箱" />
        </a-form-model-item>
        <a-form-model-item prop="password">
          <a-input v-model="loginForm.password" class="input" type="password" placeholder="请输入密码">
            <!-- <span slot="suffix" class="forget"></span> -->
            <span slot="suffix" class="forget">
              <a-tooltip placement="right">
                <template slot="title" >忘记密码，<router-link :to="{name:'ForgetPassword'}">点这里</router-link></template>
                <a-icon type="question-circle" />
              </a-tooltip>
            </span>
          </a-input>
        </a-form-model-item>
        <a-form-model-item style="text-align:center">
          <a-button type="primary" @click="login" block>登录</a-button>
        </a-form-model-item>
        <div class="operation">
          没有账号？
          <router-link :to="{
            name: 'Register'
          }">立即注册</router-link>
        </div>
      </a-form-model>
    </div>
  </div>
</template>
<script>
import userApi from "@/api/user";
export default {
  data() {
    const patternEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
    return {
      loginForm: {
        email: "474070613@qq.com", // 邮箱
        password: "18993867031+-ctl", // 密码
      },
      rules: {
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          {
            pattern: patternEmail,
            message: "邮箱格式不正确",
            trigger: "blur",
          },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  created() {
    if (this.$route.params.email) {
      this.loginForm.email = this.$route.params.email;
    }
  },
  methods: {
    async login() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          // 表单校验通过
          userApi.login(this.loginForm).then((res) => {
            if (res.status === "success") {
              // 登录成功
              this.$store.dispatch("user/login", res.data);
              this.$message.success(res.msg);
              this.$router.push({
                name: "Index",
              });
            } else {
              // 登录失败
              this.$message.error(res.msg);
            }
          });
        }
      });
    },
  },
};
</script>

