import axs from '@/axios';

export default {
  // 用户登录
  login(params) {
    return axs.post('/passport/login', params);
  },
  // 用户注销
  logout() {
    return {
      data: null,
      msg: '注销成功',
      status: 'success',
    };
  },
  // 获取验证码
  getCode(email) {
    return axs.post('/passport/getCode', { email });
  },
  // 用户注册
  register(params) {
    return axs.post('/passport/logon', params);
  },
  // 找回密码
  findBack(params) {
    return axs.post('/passport/findBack', params);
  },
  // 修改用户信息
  changeInfo(params) {
    return axs.put('/passport/changeUserInfo', params);
  },
};
