import * as userCookie from '@/utils/cookie/userCookie';

export default {
  namespaced: true,
  state: {
    user: userCookie.get(),
    loading: false,
  },
  mutations: {
    setUser(state, payload) {
      state.user = payload;
    },
    setLoading(state, payload) {
      state.loading = payload;
    },
  },

  actions: {
    login(ctx, payload) {
      ctx.commit('setUser', payload);
      userCookie.set(payload);
    },
    logout(ctx) {
      ctx.commit('setUser', null);
      userCookie.remove();
    },
  },
};
