import Vue from 'vue';
import Router from 'vue-router';
import routes from './routes';
import store from '@/store';
import getMenuRoutes from '@/utils/permission';

Vue.use(Router);

const ayncRouterMap = [{
  path: '/product',
  name: 'Product',
  meta: {
    title: '商品',
    icon: 'shop',
  },
  component: () => import('@/views/layout/Home.vue'),
  children: [{
    path: 'list',
    name: 'ProductList',
    meta: {
      title: '商品列表',
      icon: 'bars',
    },
    component: () => import('@/views/page/product/product.vue'),
  }, {
    path: 'category',
    name: 'Category',
    meta: {
      title: '类目管理',
      icon: 'control',
    },
    component: () => import('@/views/page/category/category.vue'),
  }],
}];

const router = new Router({
  routes,
});

let isAddRoutes = false;
// 前置守卫
router.beforeEach((to, from, next) => {
  if (to.meta.power) {
    // 需要鉴权
    if (store.state.user.user) {
      if (!isAddRoutes) {
        const menuRoutes = getMenuRoutes(store.state.user.user.role, ayncRouterMap);
        store.dispatch('menu/changeMenu', routes.concat(menuRoutes)).then(() => {
          menuRoutes.forEach((item) => {
            router.addRoute(item);
          });
          return next();
        });
        isAddRoutes = true;
      }
      return next();
    }
    return next({
      name: 'Login',
    });
  }
  return next();
});

// 解决跳转报错
const originalPush = Router.prototype.push;
const originalReplace = Router.prototype.replace;
// push
Router.prototype.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject);
  return originalPush.call(this, location).catch((err) => err);
};
// replace
Router.prototype.replace = function push(location, onResolve, onReject) {
  if (onResolve || onReject) return originalReplace.call(this, location, onResolve, onReject);
  return originalReplace.call(this, location).catch((err) => err);
};

export default router;
