export default {
  set(name, value) {
    document.cookie = `${name}=${value}`;
  },
  remove(name) {
    this.set(name, '', 0);
  },
  get(name) {
    const cookieArr = document.cookie.split(';');
    for (let i = 0; i < cookieArr.length; i += 1) {
      const item = cookieArr[i].trim().split('=');
      if (item[0] === name) return item[1];
    }
    return null;
  },
};
