<template>
  <div class="register-container">
    <div class="box">
      <h2>账号注册</h2>
      <a-form-model :model="form" ref="form" :rules="rules" layout="vertical">
        <a-form-model-item label="用户名" prop="username">
          <a-input v-model="form.username" />
        </a-form-model-item>
        <a-form-model-item label="邮箱" prop="email">
          <a-input v-model="form.email" />
        </a-form-model-item>
        <a-form-model-item label="密码" prop="password">
          <a-input v-model="form.password" />
        </a-form-model-item>
        <a-form-model-item label="验证码" prop="code">
          <a-input v-model="form.code" :maxLength="6">
            <span slot="suffix" class="get-code" @click="getCode">获取验证码</span>
          </a-input>
        </a-form-model-item>
        <a-form-model-item label="角色">
          <a-select v-model="form.role" placeholder="please select your zone">
            <a-select-option value="customer">普通用户</a-select-option>
            <a-select-option value="admin">管理员</a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item>
          <a-button type="primary" @click="onSubmit" block>注册</a-button>
        </a-form-model-item>
        <div class="login">
          已有账号，
          <span @click="toLogin">去登录</span>
        </div>
      </a-form-model>
    </div>
  </div>
</template>

<script>
import userApi from "@/api/user";
import reg from "@/utils/public/regular";
export default {
  data() {
    return {
      form: {
        email: "", // 邮箱
        password: "", // 密码
        code: "", // 验证码
        username: "", // 用户名
        role: "customer", // 角色
      },
      rules: {
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          {
            pattern: reg.email,
            message: "请输入正确的邮箱格式",
            trigger: "blur",
          },
        ],
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        code: [{ required: true, message: "请输入验证码", trigger: "blur" }],
      },
    };
  },
  methods: {
    onSubmit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          userApi.register(this.form).then((res) => {
            if (res.status === "success") {
              // 注册成功
              this.$message.success(res.msg);
              this.toLogin();
            } else {
              this.$message.warn(res.msg);
            }
          });
        }
      });
    },
    // 获取验证码
    async getCode() {
      this.$refs.form.validateField("email", (valid) => {
        if (!valid) {
          userApi.getCode(this.form.email).then((res) => {
            if (res.status === "success") {
              this.$message.success("验证码发送成功");
            } else {
              this.$message.warn(res.msg);
            }
          });
        }
      });
    },
    // 去登录
    toLogin() {
      this.$router.push({
        name: "Login",
        params: {
          email: this.form.email,
        },
      });
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/assets/css/mixin.less";
.register-container {
  .flex-center();
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #001529, #777);
  .box {
    width: 450px;
    border-radius: 10px;
    box-shadow: 1px 1px 5px 1px #fff;
    padding: 20px 40px;
    h2 {
      text-align: center;
      color: #fff;
      margin-bottom: 30px;
    }
    .login {
      color: #fff;
      text-align: center;
      font-size: 12px;
      span {
        cursor: pointer;
        color: #30a679;
        &:hover {
          color: lighten(#30a679, 10%);
        }
      }
    }
    .get-code {
      cursor: pointer;
      user-select: none;
      font-weight: 600;
      color: #30a679;
      &:hover {
        color: lighten(#30a679, 10%);
      }
    }
  }
}

.register-container /deep/ .ant-form-item-label > label {
  color: #fff !important;
}
</style>